// 移动端头部
.mobile-header-wrapper {
  width: 100%;
  padding: 16px 20px;
  box-sizing: border-box;
  background: var(--gradientColor);

  &.in-home {
    padding: 22px 20px;
  }

  .header-top {
    height: 28px;
    display: flex;
    align-items: center;
    justify-content: space-between;

    .logo {
      height: 24px;

      .freelog {
        height: 24px;
        font-size: 24px;
        display: flex;
        align-items: center;
        justify-content: center;
        font-weight: normal;
        margin-right: 5px;
      }
    }

    .header-top-left {
      display: flex;
      align-items: center;

      .back-arrow {
        width: 7px;
        height: 12px;
      }

      .back-label {
        font-size: 16px;
        color: #ffffff;
        margin-left: 10px;
      }
    }

    .header-top-right {
      display: flex;
      align-items: center;
      justify-content: flex-end;
      margin-left: 10px;

      .fl-icon-content {
        width: 20px;
        height: 20px;
        color: rgba(255, 255, 255, 0.6);
        font-size: 20px;
        display: flex;
        align-items: center;
        justify-content: center;
      }

      .menu {
        width: 42px;
        height: 32px;
        margin-left: 30px;
      }
    }
  }

  .search-box {
    width: 100%;
    height: 42px;
    border-radius: 42px;
    display: flex;
    align-items: center;
    background: rgba(255, 255, 255, 0.1);
    margin-top: 36px;

    .fl-icon-content {
      width: 14px;
      height: 14px;
      font-size: 14px;
      margin-left: 15px;
      display: flex;
      align-items: center;
      justify-content: center;
      color: rgba(255, 255, 255, 0.6);
    }
  }

  .modal {
    position: fixed;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0, 0, 0, 0.4);
    z-index: 101;
  }

  .user-box-body {
    position: fixed;
    left: 0;
    top: 0;
    bottom: 0;
    width: 340px;
    background: #ffffff;
    border-radius: 0px 10px 10px 0px;
    overflow: hidden;
    display: flex;
    flex-direction: column;
    z-index: 101;

    .user-box-top {
      position: relative;
      width: 100%;
      height: 194px;
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      background: var(--gradientColor);

      .avatar {
        width: 72px;
        height: 72px;
        border-radius: 50%;
        border: 1px solid rgba(255, 255, 255, 0.4);
      }

      .username {
        font-size: 16px;
        line-height: 22px;
        color: #fff;
        font-weight: bold;
        margin-top: 20px;
      }

      .close-btn {
        position: absolute;
        right: 31px;
        top: 31px;
        width: 12px;
        height: 12px;
        display: flex;
        align-items: center;
        justify-content: center;

        .freelog {
          font-size: 12px;
          color: #fff;
          display: flex;
          align-items: center;
          justify-content: center;
        }
      }
    }

    .btns {
      width: 100%;
      flex: 1;
      display: flex;
      flex-direction: column;
      padding: 25px 20px 0;
      box-sizing: border-box;

      .menu-btns {
        flex: 1;

        .btn {
          width: 100%;
          height: 52px;
          border-radius: 4px;
          color: #222;
          background-color: #fff;
          display: flex;
          align-items: center;

          &.active,
          &:active {
            color: var(--deriveColor);
            background: rgba(93, 145, 145, 0.05);
          }

          & + .btn {
            margin-top: 10px;
          }

          .freelog {
            font-size: 16px;
            margin: 0 11px;
            display: flex;
            align-items: center;
            justify-content: center;
            margin-top: 3px;
          }

          .btn-label {
            font-size: 16px;
          }
        }
      }

      .footer-btn {
        width: 100%;
        height: 102px;
        border-top: 1px solid rgba(0, 0, 0, 0.1);
        color: #222;
        display: flex;
        align-items: center;

        .freelog {
          font-size: 16px;
          margin: 0 11px;
          display: flex;
          align-items: center;
          justify-content: center;
          margin-top: 3px;
        }

        .btn-label {
          font-size: 16px;
        }

        .main-btn {
          width: 100%;
          height: 48px;
          border-radius: 48px;
          display: flex;
          align-items: center;
          justify-content: center;
          font-size: 16px;
          font-weight: 600;
          color: #ffffff;
        }
      }
    }
  }

  .search-page {
    position: fixed;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    background-color: #fff;
    display: flex;
    flex-direction: column;
    z-index: 101;

    .search-page-header {
      width: 100%;
      height: 60px;
      display: flex;
      align-items: center;
      padding-left: 20px;
      box-sizing: border-box;
      background: var(--gradientColor);
      margin-bottom: 20px;

      .search-page-box {
        position: relative;
        flex: 1;
        height: 42px;
        border-radius: 42px;
        display: flex;
        align-items: center;
        overflow: hidden;

        .search-input {
          height: 100%;
          flex: 1;
          width: 0;
          font-size: 16px;
          color: #222;
          background-color: rgba(255, 255, 255, 0.1) !important;
          padding: 0 44px !important;
          transition: all 0.2s linear;

          &:active {
            background-color: rgba(255, 255, 255, 0.18) !important;
          }

          &:focus,
          &.in-focus {
            background-color: #fff !important;

            & ~ .fl-icon-content {
              color: #8e8e93;
            }
          }
        }

        .fl-icon-content {
          position: absolute;
          left: 15px;
          width: 14px;
          height: 14px;
          font-size: 14px;
          display: flex;
          align-items: center;
          justify-content: center;
          color: rgba(255, 255, 255, 0.6);
          transition: all 0.2s linear;
        }
      }

      .cancel-btn {
        font-size: 16px;
        line-height: 22px;
        height: 100%;
        padding: 0 20px;
        display: flex;
        align-items: center;
        color: #fff;

        &:active {
          opacity: 0.6;
        }
      }
    }

    .search-history-box {
      flex: 1;
      height: 0;
      width: 100%;
      display: flex;
      flex-direction: column;
      align-items: center;

      & + .search-history-box {
        margin-top: 25px;
      }

      .search-history-box-title {
        width: 100%;
        font-size: 14px;
        color: #999999;
        line-height: 20px;
        padding: 0 20px;
        box-sizing: border-box;
        display: flex;
        justify-content: space-between;

        .clear-btn {
          color: #5d9191;
        }
      }

      .search-history-box-list {
        flex: 1;
        height: 0;
        overflow-y: auto;
        width: 340px;
        margin-top: 20px;
        display: flex;
        align-content: flex-start;
        flex-wrap: wrap;

        .tag {
          min-height: 38px;
          padding: 8px 15px;
          border-radius: 38px;
          display: flex;
          align-items: center;
          word-break: break-all;
          font-size: 14px;
          color: #575e6a;
          background-color: #ebecf0;
          margin: 0 5px 15px;
          line-height: 18px;
          box-sizing: border-box;

          &:active {
            background-color: #dcdee2;
          }

          .freelog {
            font-size: 10px;
            display: flex;
            align-items: center;
            justify-content: center;
            margin-left: 10px;
          }
        }
      }
    }
  }
}

