// PC
.header-wrapper {
  width: 100%;
  padding: 0 140px;
  background: var(--gradientColor);
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  align-items: center;

  .header-box {
    width: 1160px;
    height: 70px;
    display: flex;
    align-items: center;
    justify-content: space-between;

    .header-left {
      display: flex;
      align-items: center;

      .logo {
        height: 24px;
        cursor: pointer;

        .freelog {
          height: 24px;
          font-size: 24px;
          display: flex;
          align-items: center;
          justify-content: center;
          font-weight: normal;
          margin-right: 5px;
        }
      }

      .small-search-box {
        position: relative;
        width: 240px;
        height: 32px;
        display: flex;
        align-items: center;
        margin-left: 40px;

        .search-input {
          height: 32px;
          border-radius: 32px;
          flex: 1;
          font-size: 14px;
          color: #222;
          padding: 0 34px !important;
          background-color: rgba(255, 255, 255, 0.1) !important;
          transition: all 0.2s linear;

          &:hover {
            background-color: rgba(255, 255, 255, 0.18) !important;
          }

          &:focus,
          &.in-focus {
            background-color: #fff !important;

            & ~ .fl-icon-content {
              color: #8e8e93;
            }
          }
        }

        .fl-icon-content {
          position: absolute;
          left: 10px;
          font-size: 14px;
          display: flex;
          align-items: center;
          justify-content: center;
          color: rgba(255, 255, 255, 0.6);
        }

        .clear-btn {
          position: absolute;
          right: 10px;
          font-size: 10px;
          display: flex;
          align-items: center;
          justify-content: center;
          margin-top: 2px;
        }

        .search-history {
          position: absolute;
          left: 0;
          right: 0;
          top: 100%;
          margin-top: 5px;
          background-color: #fff;
          background: #ffffff;
          box-shadow: 0px 2px 5px 0px rgba(0, 0, 0, 0.2);
          border-radius: 4px;
          padding: 4px;
          padding-bottom: 50px;
          box-sizing: border-box;
          display: flex;
          flex-direction: column;
          align-items: center;
          z-index: 1;

          .history-list {
            display: flex;
            flex-direction: column;
            width: 100%;
            min-height: 170px;
            max-height: 400px;
            overflow-y: auto;

            &::-webkit-scrollbar {
              width: 5px;
            }

            &::-webkit-scrollbar-thumb {
              width: 5px;
              border-radius: 5px;
              background-color: rgba(0, 0, 0, 0.1);
            }

            .history-item {
              position: relative;
              flex-shrink: 0;
              width: 100%;
              min-height: 34px;
              border-radius: 4px;
              padding: 8px 11px;
              box-sizing: border-box;
              display: flex;
              align-items: center;
              justify-content: space-between;
              cursor: pointer;
              transition: all 0.2s linear;

              &.catch,
              &:hover {
                background: rgba(0, 0, 0, 0.03);

                .freelog {
                  opacity: 1;
                }
              }

              .item-word {
                font-size: 12px;
                line-height: 18px;
                color: #222222;
                word-break: break-all;
                margin-right: 10px;
              }

              .delete-btn {
                width: 10px;
                height: 10px;
                font-size: 10px;
                display: flex;
                align-items: center;
                justify-content: center;
                color: #999;
                cursor: pointer;
                transition: all 0.2s linear;
                opacity: 0;

                &:hover {
                  color: #a9a9ad;
                }
              }
            }
          }

          .text-btn {
            position: absolute;
            bottom: 12px;
            font-size: 12px;
            line-height: 18px;
          }
        }
      }
    }

    .header-right {
      display: flex;
      align-items: center;

      .nav-btn {
        padding: 0 25px;
        font-size: 14px;
        line-height: 16px;
        font-weight: 600;
        color: #ffffff;
        border-right: 1px solid rgba(255, 255, 255, 0.2);
        opacity: 0.8;
        cursor: pointer;
        transition: all 0.2s linear;

        &:hover {
          opacity: 1;
        }
      }

      .user-avatar {
        position: relative;
        display: flex;
        align-items: center;
        cursor: pointer;
        margin-left: 25px;

        .avatar {
          width: 32px;
          height: 32px;
          border-radius: 50%;
          border: 1px solid rgba(255, 255, 255, 0.4);
        }

        .user-box {
          position: absolute;
          right: 0;
          top: 100%;
          padding-top: 10px;
          cursor: default;
          z-index: 1;

          .user-box-body {
            width: 240px;
            background: #ffffff;
            box-shadow: 0px 2px 5px 0px rgba(0, 0, 0, 0.2);
            border-radius: 4px;
            display: flex;
            flex-direction: column;
            align-items: center;

            .avatar {
              width: 60px;
              height: 60px;
              border-radius: 50%;
              border: 1px solid #d1d1d1;
              margin-top: 20px;
            }

            .username {
              font-size: 16px;
              line-height: 22px;
              color: #222222;
              font-weight: bold;
              margin-top: 15px;
              margin-bottom: 20px;
            }

            .btn {
              width: 100%;
              height: 50px;
              line-height: 50px;
              font-size: 14px;
              padding-left: 20px;
              box-sizing: border-box;
              border-top: 1px solid rgba(0, 0, 0, 0.05);

              &:last-child {
                border-radius: 0 0 4px 4px;
              }
            }
          }
        }
      }

      .user-btns {
        display: flex;
        margin-left: 25px;

        .btn {
          height: 32px;
          padding: 0 15px;
          box-sizing: border-box;
          border-radius: 4px;
          font-size: 14px;
          font-weight: bold;
          display: flex;
          align-items: center;
          justify-content: center;
          cursor: pointer;
        }

        .header-register-btn {
          margin-left: 10px;
        }
      }
    }
  }
}

// fade-in
.fade-in-enter {
  opacity: 0.5;
}
.fade-in-enter-active {
  opacity: 1;
  transition: all 200ms ease-out;
}
.fade-in-exit {
  opacity: 1;
}
.fade-in-exit-active {
  opacity: 0;
  transition: all 200ms ease-in;
}

// slide-down-scale
.slide-down-scale-enter {
  opacity: 0.5;
  transform: translateY(-30px) scaleY(0.8);
}
.slide-down-scale-enter-active {
  opacity: 1;
  transform: translateY(0) scaleY(1);
  transition: all 200ms ease-out;
}
.slide-down-scale-exit {
  opacity: 1;
  transform: translateY(0) scaleY(1);
}
.slide-down-scale-exit-active {
  opacity: 0;
  transform: translateY(-30px) scaleY(0.8);
  transition: all 200ms ease-in;
}

// slide-right
.slide-right-enter {
  opacity: 0;
  transform: translateX(-100%);
}
.slide-right-enter-active {
  opacity: 1;
  transform: translateX(0);
  transition: all 0.2s ease-out;
}
.slide-right-exit {
  opacity: 1;
  transform: translateX(0);
}
.slide-right-exit-active {
  opacity: 0;
  transform: translateX(-100%);
  transition: all 0.2s ease-in;
}
